<template>
  <div>
    <section v-if="!showLoading"  class="shop_container">
      <nav class="goback"><i class="fa fa-angle-left" @click.stop="goback"></i></nav>
      <header class="shop_detail_header" ref="shopheader" :style="{zIndex: showActivities? '14':'10'}">
        <img :src="imgBaseUrl + shopDetailData.image_path" class="header_cover_img">
        <section class="description_header">
          <div  class="description_top">
            <section class="description_left">
              <img :src="imgBaseUrl + shopDetailData.image_path">
            </section>
            <router-link  to="/shop/shopDetail" >
              <section class="description_right">
                <h4 class="description_title ellipsis">{{shopDetailData.name}}</h4>
                <p class="description_text">{{shopDetailData.order_lead_time}}{{shopDetailData.float_delivery_fee}}</p>
                <p class="description_promotion ellipsis">{{promotionInfo}}</p>
              </section>
              <i class="header-right-icon fa fa-angle-right"></i>
            </router-link>
          </div>
          <footer class="description_footer" v-if="shopDetailData.activities.length" @click="showActivitiesFun">
            <p class="ellipsis">
              <span class="tip_icon" :style="{backgroundColor: '#' + shopDetailData.activities[0].icon_color, borderColor: '#' + shopDetailData.activities[0].icon_color}">{{shopDetailData.activities[0].icon_name}}</span>
              <span>{{shopDetailData.activities[0].description}}APP</span>
            </p>
            <p>{{shopDetailData.activities.length}}</p>
            <i class="footer-right-icon fa fa-angle-right"></i>
          </footer>
        </section>
      </header>
      <!--  //-->
      <transition name="fade">
        <section class="activities_details" v-if="showActivities">
          <h2 class="activities_shoptitle">{{shopDetailData.name}}</h2>
          <h3 class="activities_ratingstar">
            <yd-rate slot="left" color="#ff6000" v-model='RATING'></yd-rate>
          </h3>
          <section class="activities_list">
            <header class="activities_title_style"><span></span></header>
            <ul>
              <li v-for="item in shopDetailData.activities" :key="item.id">
                <span class="activities_icon" :style="{backgroundColor: '#' + item.icon_color, borderColor: '#' + item.icon_color}">{{item.icon_name}}</span>
                <span>{{item.description}}APP</span>
              </li>
            </ul>
          </section>
          <section class="activities_shopinfo">
            <header class="activities_title_style"><span></span></header>
            <p>{{promotionInfo}}</p>
          </section>
          <span class="close_activities">
          <i class=" fa fa-remove" @click.stop="showActivitiesFun"></i>
       </span>
        </section>
      </transition>
      <switches  @switch="doSwitches" :currentIndex="currentIndex" :switches="switches" class="change_show_type"></switches>
      <!---->
      <transition name="fade-choose">
        <section class="food_container" v-show="currentIndex==0">
          <section class="menu_container">
            <!---->
              <scroll class="menu_left" :data="menuList" :probe-type="probeType" :listen-scroll="listenScroll" ref="menu_left">
                <ul>
                  <li v-for="(item,index) in menuList" :key="index" class="menu_left_li" :class="{activity_menu: index == menuIndex}" @click="chooseMenu(index)">
                    <img :src="getImgPath(item.icon_url)" v-if="item.icon_url">
                    <span>{{item.name}}</span>
                    <yd-badge type="danger" class="category_num" v-if="categoryNum[index]&&item.type==1">{{categoryNum[index]}}</yd-badge>
                    <!--<span class="category_num" v-if="categoryNum[index]&&item.type==1">{{categoryNum[index]}}</span>-->
                  </li>
                </ul>
              </scroll>
            <!-- -->
            <scroll class="menu_right" ref="menuFoodList" @scroll="rightMenuScroll" :data="menuList" :probe-type="probeType" :listen-scroll="listenScroll">
              <ul ref="menulis">
                <li v-for="(item,index) in menuList" :key="index" ref="foodList">
                  <header class="menu_detail_header">
                    <section class="menu_detail_header_left">
                      <strong class="menu_item_title">{{item.name}}</strong>
                      <span class="menu_item_description">{{item.description}}</span>
                    </section>
                    <span class="menu_detail_header_right"  @click.stop="showTitleDetail(index)">
                    <p class="description_tip" v-if="index === TitleDetailIndex">
                      <span>{{item.name}}</span>
                      {{item.description}}
                    </p>
                 </span>
                  </header>
                  <!---->
                  <section  v-for="(foods,foodindex) in item.foods" :key="foodindex" class="menu_detail_list">
                    <router-link  :to="{path: 'shop/foodDetail', query:{image_path:foods.image_path,
                 description: foods.description, month_sales: foods.month_sales,
                 name: foods.name, rating: foods.rating,
                 rating_count: foods.rating_count, satisfy_rate: foods.satisfy_rate,
                 foods, shopId}}" tag="div" class="menu_detail_link">
                      <section class="menu_food_img">
                        <img :src="imgBaseUrl + foods.image_path">
                      </section>
                      <section class="menu_food_description">
                        <h3 class="food_description_head">
                          <strong class="description_foodname">{{foods.name}}</strong>
                          <ul v-if="foods.attributes.length" class="attributes_ul">
                            <li v-for="(attribute,foodindex) in foods.attributes"
                                :key="foodindex" :style="{color:'#'+ attribute.icon_color,
                             borderColor:'#'+attribute.icon_color}"
                                :class="{attribute_new: attribute.icon_name == ''}"
                            >
                              <p :style="{color:attribute.icon_name == ''? '#fff' : '#' + attribute.icon_color}">
                                {{attribute.icon_name == '' ? '':attribute.icon_name}}
                              </p>
                            </li>
                          </ul>
                        </h3>
                        <p class="food_description_content">{{foods.description}}</p>
                        <p class="food_description_sale_rating">
                          <span>{{foods.month_sales}}</span>
                          <span>{{foods.satisfy_rate}}%</span>
                        </p>
                        <p v-if="foods.activity" class="food_activity">
                          <span :style="{color: '#' + foods.activity.image_text_color,borderColor:'#' +foods.activity.icon_color}">{{foods.activity.image_text}}</span>
                        </p>
                      </section>
                    </router-link>
                    <footer class="menu_detail_footer">
                      <section class="food_price">
                        <span></span>
                        <span>{{foods.specfoods[0].price}}</span>
                        <span v-if="foods.specifications.length"></span>
                      </section>
                      <buy-cart :shopId='shopId' :foods='foods' @showMoveDot="showMoveDotFun" @showChooseList="showChooseList"  @showReduceTip="showReduceTip"></buy-cart>
                    </footer>
                  </section>
                </li>
              </ul>
            </scroll>
          </section>
          <!---->
          <section class="buy_cart_container">
            <!-- totalPrice: 0, //
             receiveInCart: false, //-->
            <section class="cart_icon_num" @click.stop="toggleCartList">
              <!--,-->
              <div class="cart_icon_container" ref="cartContainer" :class="{'cart_icon_activity': totalPrice > 0, 'move_in_cart':receiveInCart}">
                <yd-badge class="cart_list_length" type="danger" v-if="totalNum">{{totalNum}}</yd-badge>
                     <i class="cart_icon fa fa-shopping-cart"></i>
              </div>
              <div class="cart_num" v-show="totalPrice">
                <div> {{totalPrice}}</div>
                <div>{{deliveryFee}}</div>
              </div>
            </section>
            <section class="gotopay" :class="{'gotopay_acitvity': minimumOrderAmount <= 0}">
              <span class="gotopay_button_style" v-if="minimumOrderAmount > 0">{{minimumOrderAmount}}</span>
              <router-link v-else :to="{path:'/confirmOrder',query:{geohash, shopId}}" class="gotopay_button_style">
                
              </router-link>
            </section>
          </section>
          <!--zzz-->
          <transition name="toggle-cart">
            <section class="cart_food_list" v-show="(showCartList&&cartFoodList.length)">
              <header>
                <h4></h4>
                <div @click.stop="clearCart" class="clearCart">
                  <i class="fa fa-trash-o"></i>
                  <span class="clear_cart"></span>
                </div>
              </header>
              <scroll class="cart_food_details" :data="cartFoodList">
                <ul>
                  <li v-for="(item,index) in cartFoodList" :key="index" class="cart_food_li">
                    <div class="cart_list_num">
                      <p class="ellipsis">{{item.name}}</p>
                      <p class="ellipsis">{{item.specs}}</p>
                    </div>
                    <div class="cart_list_price">
                      <span></span>
                      <span>{{item.price}}</span>
                    </div>
                    <section class="cart_list_control">
                      <span @click.stop="removeOutCart(item.category_id, item.item_id, item.food_id, item.name, item.price, item.specs)">
                           <i class="cart-minus fa fa-minus-circle" ></i>
                      </span>
                      <span class="cart_num">{{item.num}}</span>
                      <i class="add_icon fa fa-plus-circle" @click.stop="addToCart(item.category_id, item.item_id, item.food_id, item.name, item.price, item.specs)"></i>
                    </section>
                  </li>
                </ul>
              </scroll>
            </section>
          </transition>
          <!-- -->
          <transition name="fade">
            <div class="screen_cover" v-show="showCartList&&cartFoodList.length" @click="toggleCartList"></div>
          </transition>
        </section>
      </transition>
      <transition  name="fade-choose">
        <section   v-show="currentIndex===1">
          <ratings :ratingTagsList=ratingTagsList :shopDetailData=shopDetailData :ratingScoresData=ratingScoresData :shopId=shopId>
          </ratings>
        </section>
      </transition>
    </section>
    <!--zzz-->
    <section>
      <transition name="fade">
        <div class="specs_cover" @click="showChooseList" v-if="showSpecs"></div>
      </transition>
      <transition name="fadeBounce">
        <div class="specs_list" v-if="showSpecs">
          <header class="specs_list_header">
            <h4 class="ellipsis">{{choosedFoods.name}}</h4>
            <i class="specs_cancel fa fa-remove" @click.stop="showChooseList"></i>
          </header>
          <section class="specs_details">
            <h5 class="specs_details_title">{{choosedFoods.specifications[0].name}}</h5>
            <ul>
              <li v-for="(item, itemIndex) in choosedFoods.specifications[0].values" :class="{'specs_activity': itemIndex == specsIndex}" @click.stop="chooseSpecs(itemIndex)">
                {{item}}
              </li>
            </ul>
          </section>
          <footer class="specs_footer">
            <div class="specs_price">
              <span> </span>
              <span>{{choosedFoods.specfoods[specsIndex].price}}</span>
            </div>
            <div class="specs_addto_cart"  @click.stop="addSpecs(choosedFoods.category_id, choosedFoods.item_id, choosedFoods.specfoods[specsIndex].food_id, choosedFoods.specfoods[specsIndex].name, choosedFoods.specfoods[specsIndex].price, choosedFoods.specifications[0].values[specsIndex], choosedFoods.specfoods[specsIndex].packing_fee, choosedFoods.specfoods[specsIndex].sku_id, choosedFoods.specfoods[specsIndex].stock)"></div>
          </footer>
        </div>
      </transition>
    </section>
    <section>
      <!-- -->
      <transition name="fade">
        <p class="show_delete_tip" v-if="showDeleteTip"></p>
      </transition>
     <!--  appear showMoveDot:key
     -->
      <transition
        appear
        @after-appear = "afterEnter"
        @before-appear="beforeEnter"
        v-for="(item,index) in showMoveDot"
        :key="index"
      >
            <span class="move_dot" v-if="item">
                <i class="add_icon fa fa-plus-circle move-ball"></i>
            </span>
      </transition>
      <section class="animation_opactiy shop_back_svg_container" v-if="!menuList.length">
        <img src="../../images/shop_back_svg.svg">
      </section>
    </section>
    <div ref="loading" class="loading-container" v-show="showLoading">
      <loading></loading>
    </div>
    <transition name="router-slid" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
import {mapGetters,mapMutations} from 'vuex'
import headTop from 'components/header/head'
import buyCart from 'components/common/buycart/buyCart'
import ratings from 'src/page/ratings/ratings'
import switches from 'components/common/switches/switches'
import loading from 'components/common/loading/loading'
import scroll from 'components/common/scroll/scroll'
import {getImgPath} from 'src/components/common/mixin'
import {imgBaseUrl} from 'src/config/env'
import {msiteAdress, foodMenu,ratingScores,ratingTags,getRatingList,shopDetails} from 'src/service/getData'
//shopDetails
/*import {shopDetails} from 'src/service/data/shop'*/
/*3d*/
import {prefixStyle} from 'src/config/dom'
const transform = prefixStyle('transform');
  export default {
    mixins: [getImgPath],
  data(){
     return{
       geohash: '', //geohash
       imgBaseUrl,
       changeShowType: 'food',//
       showLoading: true, //,classshop_containerdom
       currentIndex:0,
       showDeleteTip: false, //
       showActivities: false, //
       shopListTop: [], //
       TitleDetailIndex: null, //
       shopDetailData: null, //
       ratingScoresData: null, //
       ratingTagsList: null, //
       shopId: null, //id
       menuList: [], //
       menuIndex: 0, //0
       categoryNum: [], //
       choosedFoods: [], //
       totalPrice: 0, //
       cartFoodList: [], //
       receiveInCart: false, //
       menuIndexChange: true,//indexscrollindexbug
       showCartList: false,//
       showSpecs: false,//
       specsIndex: 0, //
       rightMenuScrollY:'',//y
       showMoveDot: [], //
       elLeft: 0, //top
       elBottom: 0, //bottom
       windowHeight: null, //
       RATING:4,//
       switches:[
         {name:''},
         {name:''}
       ]
     }
   },
   created(){
    // domel
    this.probeType = 3;
    this.listenScroll = true;
    this.geohash = this.$route.query.geohash;
    this.shopId = parseInt(this.$route.query.id);
   },
   mounted(){
     this.initData();
     this.windowHeight=window.innerHeight;
   },
   computed: {
     ...mapGetters([
       'latitude','longitude','cartList'
     ]),
     //
     deliveryFee: function () {
       if (this.shopDetailData) {
         return this.shopDetailData.float_delivery_fee;
       }else{
         return null;
       }
     },
     //
     shopCart (){
       return {...this.cartList[this.shopId]};
     },
     //
     totalNum(){
       let num = 0;
       this.cartFoodList.forEach(item => {
         num += item.num
       })
       return num
     },
     //
     minimumOrderAmount(){
       if (this.shopDetailData) {
         return this.shopDetailData.float_minimum_order_amount - this.totalPrice;
       }else{
         return null;
       }
     },
     promotionInfo(){
       return this.shopDetailData.promotion_info || ''
     }
   },
   methods: {
     ...mapMutations([
       'RECORD_ADDRESS','ADD_CART','REDUCE_CART','CLEAR_CART','RECORD_SHOPDETAIL'
     ]),
     doSwitches(index){
       this.currentIndex = index;
       if(this.index ===0){
         this.changeShowType = 'food';
       }else{
         this.changeShowType = 'rating';
       }
     },
    rightMenuScroll(pos){
       this.rightMenuScrollY = pos.y;
     },
     //
    chooseMenu(index){
      this.menuIndex = index;
      this.$refs.menuFoodList.scrollTo(0, -this.shopListTop[index], 400);
    },
     //7idididid
     addToCart(category_id, item_id, food_id, name, price, specs){
       this.ADD_CART({shopid: this.shopId, category_id, item_id, food_id, name, price, specs});
     },
     //7idididid
     removeOutCart(category_id, item_id, food_id, name, price, specs){
       this.REDUCE_CART({shopid: this.shopId, category_id, item_id, food_id, name, price, specs});
     },
     //
     clearCart(){
       this.toggleCartList();
       this.CLEAR_CART(this.shopId);
     },
    showTitleDetail(index){
       if (this.TitleDetailIndex == index) {
         this.TitleDetailIndex = null;
       }else{
         this.TitleDetailIndex = index;
       }
     },
     //
     toggleCartList(){
       this.cartFoodList.length ? this.showCartList = !this.showCartList : true;
     },
     //
     chooseSpecs(index){
       this.specsIndex = index;
     },
     //
    showChooseList(foods){
      if (foods) {
        this.choosedFoods = foods;
      }
      this.showSpecs = !this.showSpecs;
      this.specsIndex = 0;
    },
     //
     showReduceTip(){
       this.showDeleteTip = true;
       clearTimeout(this.timer);
       this.timer = setTimeout(() => {
         clearTimeout(this.timer);
         this.showDeleteTip = false;
       }, 2000);
     },
     //
     addSpecs(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock){
       this.ADD_CART({shopid: this.shopId, category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock});
       this.showChooseList();
     },
    showActivitiesFun(){
      this.showActivities = !this.showActivities;
    },
     //
    showMoveDotFun(showMoveDot, elLeft, elBottom){
      this.showMoveDot = [...this.showMoveDot, ...showMoveDot];
      this.elLeft = elLeft;
      this.elBottom = elBottom;
    },
     beforeEnter(el){
      //
       // 
      el.style.transform = `translate3d(0,${30+ this.elBottom - this.windowHeight}px,0)`;
      el.children[0].style.transform = `translate3d(${this.elLeft - 30}px,0,0)`;
      el.children[0].style.opacity = 0;
     },
     afterEnter(el){
       el.style.transform = `translate3d(0,0,0)`;
       el.children[0].style.transform = `translate3d(0,0,0)`;
       el.style.transition = 'transform .55s cubic-bezier(0.3, -0.25, 0.7, -0.15)';
       el.children[0].style.transition = 'transform .55s linear';
       this.showMoveDot = this.showMoveDot.map(item => false);
       el.children[0].style.opacity = 1;
       el.children[0].addEventListener('transitionend', () => {
         this.listenInCart();
       })
       el.children[0].addEventListener('webkitAnimationEnd', () => {
         this.listenInCart();
       })
     },
     //
     listenInCart(){
       if (!this.receiveInCart) {
         this.receiveInCart = true;
         this.$refs.cartContainer.addEventListener('animationend', () => {
           this.receiveInCart = false;
         })
         this.$refs.cartContainer.addEventListener('webkitAnimationEnd', () => {
           this.receiveInCart = false;
         })
       }
     },
    async initData(){
      if (!this.latitude) {
        //
        console.log('this.geohash='+this.geohash)
        let res = await msiteAdress(this.geohash);
        // vuex
        this.RECORD_ADDRESS(res);
      }
      //
      //
       await shopDetails(this.shopId, this.latitude, this.longitude).then((res)=>{
         this.shopDetailData = res ;
         this.RECORD_SHOPDETAIL(res)
         //dom
         /*this.getFoodListHeight();*/
      });
     /* this.shopDetailData =shopDetails;*/
     /* this.RECORD_SHOPDETAIL(shopDetails)*/
      //
      this.menuList = await foodMenu(this.shopId);
      //
      this.ratingScoresData = await ratingScores(this.shopId);
      //Tag
      this.ratingTagsList = await ratingTags(this.shopId);

      //,classshop_containerdom
      this.hideLoading();
    },
     /**
      * shopCart categoryNumtotalPricecartFoodListvuex
      */
     initCategoryNum(){
       let newArr = [];
       let cartFoodNum = 0;
       this.totalPrice = 0;
       //
       this.cartFoodList = [];
       //
       this.menuList.forEach((item, index) => {
         //
         if (this.shopCart&&this.shopCart[item.foods[0].category_id]) {
           let num = 0;
           //https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/Object/keys
           //Object.keys 
           Object.keys(this.shopCart[item.foods[0].category_id]).forEach(itemid => {
             Object.keys(this.shopCart[item.foods[0].category_id][itemid]).forEach(foodid => {
               let foodItem = this.shopCart[item.foods[0].category_id][itemid][foodid];
               num += foodItem.num;
               if (item.type == 1) {
                 this.totalPrice += foodItem.num*foodItem.price;
                 if (foodItem.num > 0) {
                   this.cartFoodList[cartFoodNum] = {};
                   this.cartFoodList[cartFoodNum].category_id = item.foods[0].category_id;
                   this.cartFoodList[cartFoodNum].item_id = itemid;
                   this.cartFoodList[cartFoodNum].food_id = foodid;
                   this.cartFoodList[cartFoodNum].num = foodItem.num;
                   this.cartFoodList[cartFoodNum].price = foodItem.price;
                   this.cartFoodList[cartFoodNum].name = foodItem.name;
                   this.cartFoodList[cartFoodNum].specs = foodItem.specs;
                   cartFoodNum ++;
                 }
               }
             })
           });
           newArr[index] = num;
         }else{
           newArr[index] = 0;
         }
       });
       this.totalPrice = this.totalPrice.toFixed(2);
       this.categoryNum = [...newArr];
     },
     //shopListTop
     getFoodListHeight(){
      //!
      const rightMenuFoodList = this.$refs.menuFoodList;
      //ES6Arrayfrom
       /*const listArr = Array.from(listContainer.children[0].children);
       listArr.forEach((item, index) => {
         this.shopListTop[index] = item.offsetTop;
       });*/
       const listArr = this.$refs.menulis.children;
       //console.log(typeof listArr)//object
       //forEach 
      /* listArr.forEach((item, index) => {
         this.shopListTop[index] = item.offsetTop;
       })*/
       //object
      /* for(let i in listArr)
        {
         if(typeof listArr[i]==='object'){
           this.shopListTop[i] = listArr[i].offsetTop;
         }
        }*/
      /**/
      let foodList = this.$refs.foodList;
      let height = 0;
      this.shopListTop.push(height);
      for(let i = 0;i<foodList.length;i++){
        let item = foodList[i];
        height += item.clientHeight;
        this.shopListTop.push(height);
      }
      this.onListenScroll(rightMenuFoodList)
     },

     onListenScroll(ele){
       /*scroll
       this.foodScroll = new BScroll(ele,{
         probeType: 3,
         deceleration: 0.001,
         bounce: false,
         swipeTime: 2000,
         click: true,
       });
       const menuleft = new BScroll(this.$refs.menuleft, {
         click: true,
       });*/
     },
     //
     hideLoading(){
       this.showLoading = false;
     },
     goback(){
       this.$router.go(-1);
     }
   },
   components:{
     ratings,
     buyCart,
     scroll,
     loading,
     switches,
    headTop
   },
    watch:{
      showLoading(newV){
        //dom
        if(!newV){
          this.$nextTick(()=>{
            this.getFoodListHeight();
            this.initCategoryNum();
          })
        }
      },
      rightMenuScrollY(newY){
        let scrollH =  Math.abs(Math.round(newY));
        if (!this.$refs.menu_left) {
          return
        }
        for(let i = 0; i < this.shopListTop.length; i++){
          let height1 = this.shopListTop[i];
          let height2 = this.shopListTop[i + 1];
          if (!height2 || (scrollH >= height1 && scrollH < height2)) {
            this.menuIndex = i;
            break;
          }
        }
        const leftScroll = this.$refs.menu_left;
        const menuList = leftScroll.$el.querySelectorAll('.activity_menu');
        const el = menuList[0];
        leftScroll.scrollToElement(el, 800, 0,true);
      },
      shopCart: function (value){
        this.initCategoryNum();
      },
    }
  }
</script>
<style scoped lang="scss" rel="stylesheet/scss">
@import '../../style/mixin';
@import "../../style/css/font-awesome.min.css";
@keyframes mymove{
  0%   { transform: scale(1) }
  25%  { transform: scale(.8) }
  50%  { transform: scale(1.1) }
  75%  { transform: scale(.9) }
  100% { transform: scale(1) }
}
@-moz-keyframes mymove{
  0%   { transform: scale(1) }
  25%  { transform: scale(.8) }
  50%  { transform: scale(1.1) }
  75%  { transform: scale(.9) }
  100% { transform: scale(1) }
}
@-webkit-keyframes mymove{
  0%   { transform: scale(1) }
  25%  { transform: scale(.8) }
  50%  { transform: scale(1.1) }
  75%  { transform: scale(.9) }
  100% { transform: scale(1) }
}
@-o-keyframes mymove{
  0%   { transform: scale(1) }
  25%  { transform: scale(.8) }
  50%  { transform: scale(1.1) }
  75%  { transform: scale(.9) }
  100% { transform: scale(1) }
}
.add_icon,.cart-minus{
  position: relative;
  z-index: 999;
  font-size: .8rem;
  color: $blue;
}
.shop_back_svg_container{
  position: fixed;
  @include wh(100%, 100%);
  img{
    @include wh(100%, 100%);
  }
}
.shop_container{
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0;
  left: 0;
  height: 100%;
}
.goback{
  position:fixed;
  top:0.5rem;
  z-index: 100;
  padding-top: 0.2rem;
  padding-left: 0.2rem;
  .fa{
    font-size:1.5rem;
    color:$icon;
  }
}
.clearCart{
  .fa{
    color:$icon;
    font-size: .6rem;
  }
}
.shop_detail_header{
  overflow: hidden;
  position: relative;
  .header_cover_img{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    filter: blur(10px);
  }
  .description_header{
    position: relative;
    z-index: 10;
    background-color: rgba(119,103,137,.43);
    padding: 0.4rem 0 0.4rem 0.4rem;
    width: 100%;
    overflow: hidden;
    .description_top{
      display: flex;
      .description_left{
        margin-right: 0.3rem;
        img{
          @include wh(2.9rem, 2.9rem);
          display: block;
          border-radius: 0.15rem;
        }
      }
      .description_right{
        flex: 1;
        .description_title{
          @include sc(.8rem, #fff);
          font-weight: bold;
          width: 100%;
          margin-bottom: 0.3rem;
        }
        .description_text{
          @include sc(.5rem, #fff);
          margin-bottom: 0.3rem;
        }
        .description_promotion{
          @include sc(.5rem, #fff);
          width: 11.5rem;
        }

      }
      .description_arrow{
        @include ct;
        right: 0.3rem;
        z-index: 11;
      }
    }
    .header-right-icon{
      position: absolute;
      font-size:1.5rem ;
      top: 50%;
      transform: translateY(-50%);
      right: 0.3rem;
      z-index: 11;
      color:$fc;
    }
    .footer-right-icon{
      font-size:.8rem ;
      z-index: 11;
      color:$fc;
    }
    .description_footer{
      @include fj;
      margin-top: 0.5rem;
      padding-right: 1rem;
      p{
        @include sc(.5rem, #fff);
        span{
          color: #fff;
        }
        .tip_icon{
          padding: 0 .04rem;
          border: 0.025rem solid #fff;
          border-radius: 0.1rem;
          font-size: .4rem;
          display: inline-block;
        }
      }
      .ellipsis{
        width: 75%;
      }
      .footer_arrow{
        @include wh(.45rem, .45rem);
        position: absolute;
        right: .3rem;
      }
    }
  }
}
.activities_details{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #262626;
  z-index: 200;
  padding: 1.25rem;
  .activities_shoptitle{
    text-align: center;
    @include sc(.8rem, #fff);
  }
  .activities_ratingstar{
    display: flex;
    justify-content: center;
    transform: scale(2.2);
    margin-top: .7rem;
  }
  .activities_list{
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    @include sc(.5rem, #fff);
    li{
      margin-bottom: .2rem;
      .activities_icon{
        padding: 0 .02rem;
        display: inline-block;
        border: 0.025rem solid #fff;
        border-radius: 0.1rem;
      }
      span{
        color: #fff;
        line-height: .6rem;
      }
    }
  }
  .activities_shopinfo{
    p{
      line-height: .7rem;
      @include sc(.5rem, #fff);
    }
  }
  .activities_title_style{
    text-align: center;
    margin-bottom: 1rem;
    span{
      @include sc(.5rem, #fff);
      border: 0.025rem solid #555;
      padding: .2rem .4rem;
      border-radius: 0.5rem;
    }

  }
  .close_activities{
    bottom: 1rem;
    @include cl;
    color: $fc;
    font-size: .8rem;
  }
}

.food_container{
  display: flex;
  flex: 1;
  padding-bottom: 2rem;
}
.menu_container{
  display: flex;
  flex: 1;
  overflow-y: hidden;
  position: relative;
  .menu_left{
    width: 3.8rem;
    .menu_left_li{
      padding: .7rem .3rem;
      border-bottom: 0.025rem solid #ededed;
      box-sizing: border-box;
      border-left: 0.15rem solid #f8f8f8;
      position: relative;
      img{
        display: inline-block;
        @include wh(.5rem, .6rem);
      }
      span{
        @include sc(.6rem, #666);
      }
      .category_num{
        position: absolute;
        top: .1rem;
        right: .1rem;
        background-color: #ff461d;
        color:$fc;
      }
    }
    .activity_menu{
      border-left: 0.15rem solid #3190e8;
      background-color: #fff;
      span:nth-of-type(1){
        font-weight: bold;
      }
    }
  }
  .menu_right{
    flex: 4;
    overflow-y: auto;
    .menu_detail_header{
      width: 100%;
      padding: .4rem;
      position: relative;
      @include fj;
      align-items: center;
      .menu_detail_header_left{
        width: 11rem;
        white-space: nowrap;
        overflow: hidden;
        .menu_item_title{
          @include sc(.7rem, #666);
          font-weight: bold;
        }
        .menu_item_description{
          @include sc(.5rem, #999);
          width: 30%;
          overflow: hidden;
        }
      }
      .menu_detail_header_right{
        @include wh(.5rem, 1rem);
        display: block;
        @include bis('../../images/icon_point.png');
        background-size: 100% .4rem;
        background-position: left center;
      }
      .description_tip{
        background-color: #39373a;
        opacity: 0.95;
        @include sc(.5rem, #fff);
        position: absolute;
        top: 1.5rem;
        z-index: 14;
        width: 8rem;
        right: .2rem;
        padding: .5rem .4rem;
        border: 1px;
        border-radius: .2rem;
        span{
          color: #fff;
          line-height: .6rem;
          font-size: .55rem;
        }
      }
      .description_tip::after{
        content: '';
        position: absolute;
        @include wh(.4rem, .4rem);
        background-color: #39373a;
        top: -.4rem;
        right: .7rem;
        transform: rotate(-45deg) translateY(.41rem);
      }
    }
    .menu_detail_list{
      background-color: #fff;
      padding: .6rem .4rem;
      border-bottom: 1px solid #f8f8f8;
      position: relative;
      overflow: hidden;
      .menu_detail_link{
        display:flex;
        .menu_food_img{
          margin-right: .4rem;
          img{
            @include wh(2rem, 2rem);
            display: block;
          }
        }
        .menu_food_description{
          width: 100%;
          .food_description_head{
            @include fj;
            margin-bottom: .2rem;
            .description_foodname{
              @include sc(.7rem, #333);
            }
            .attributes_ul{
              display: flex;
              li{
                font-size: .3rem;
                height: .6rem;
                line-height: .35rem;
                padding: .1rem;
                border: 1px solid #666;
                border-radius: 0.3rem;
                margin-right: .1rem;
                transform: scale(.8);
                p{
                  white-space: nowrap;
                }
              }
              .attribute_new{
                position: absolute;
                top: 0;
                left: 0;
                background-color: #4cd964;
                @include wh(2rem, 2rem);
                display: flex;
                align-items: flex-end;
                transform: rotate(-45deg) translate(-.1rem, -1.5rem);
                border: none;
                border-radius: 0;
                p{
                  @include sc(.4rem, #fff);
                  text-align: center;
                  flex: 1;
                }
              }
            }
          }
          .food_description_content{
            @include sc(.5rem, #999);
            line-height: .6rem;
          }
          .food_description_sale_rating{
            line-height: .8rem;
            span{
              @include sc(.5rem, #333);
            }
          }
          .food_activity{
            line-height: .4rem;
            span{
              font-size: .3rem;
              border: 1px solid currentColor;
              border-radius: 0.3rem;
              padding: .08rem;
              display: inline-block;
              transform: scale(.8);
              margin-left: -0.35rem;

            }
          }
        }
      }
      .menu_detail_footer{
        margin-left: 2.4rem;
        font-size: 0;
        margin-top: .3rem;
        @include fj;
        .food_price{
          span{
            font-family: 'Helvetica Neue',Tahoma,Arial;
          }
          span:nth-of-type(1){
            @include sc(.5rem, #f60);
            margin-right: .05rem;
          }
          span:nth-of-type(2){
            @include sc(.7rem, #f60);
            font-weight: bold;
            margin-right: .3rem;
          }
          span:nth-of-type(3){
            @include sc(.5rem, #666);
          }
        }
      }
    }
  }
}
.buy_cart_container{
  position: absolute;
  background-color: #3d3d3f;
  bottom: 0;
  left: 0;
  z-index: 13;
  display: flex;
  @include wh(100%, 2rem);
  .cart_icon_num{
    flex: 1;
    .cart_icon_container{
      display: flex;
      background-color: #3d3d3f;
      position: absolute;
      padding: .4rem;
      border: 0.18rem solid #444;
      border-radius: 50%;
      left: .5rem;
      top: -.7rem;
      .cart_icon{
        font-size: 1.2rem;
        color: $fc;
      }
      .cart_list_length{
        position: absolute;
        top: -.25rem;
        right: -.25rem;
        background-color: #ff461d;
        color:$fc;
      }
    }
    .move_in_cart{
      animation: mymove .5s ease-in-out;
    }
    .cart_icon_activity{
      background-color: #3190e8;
    }
    .cart_num{
      @include ct;
      left: 3.5rem;

      div{
        color: #fff;
      }
      div:nth-of-type(1){
        font-size: .8rem;
        font-weight: bold;
        margin-bottom: .1rem;
      }
      div:nth-of-type(2){
        font-size: .4rem;
      }
    }
  }
  .gotopay{
    position: absolute;
    right: 0;
    background-color: #535356;
    @include wh(5rem, 100%);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    .gotopay_button_style{
      @include sc(.7rem, #fff);
      font-weight: bold;
    }
  }
  .gotopay_acitvity{
    background-color: #4cd964;
  }
}
.cart_food_list{
  position: fixed;
  width: 100%;
  padding-bottom: 2rem;
  z-index: 12;
  bottom: 0;
  left: 0;
  background-color: #fff;
  header{
    @include fj;
    align-items: center;
    padding: .3rem .6rem;
    background-color: #eceff1;
    svg{
      @include wh(.6rem,.6rem);
      vertical-align: middle;
    }
    h4{
      @include sc(.7rem, #666);
    }
    .clear_cart{
      @include sc(.6rem, #666);
    }
  }
  .cart_food_details{
    background-color: #fff;
    max-height: 20rem;
    overflow-y: auto;
    .cart_food_li{
      @include fj;
      padding: .6rem .5rem;
      .cart_list_num{
        width: 55%;
        p:nth-of-type(1){
          @include sc(.7rem, #666);
          font-weight: bold;
        }
        p:nth-of-type(2){
          @include sc(.4rem, #666);
        }
      }
      .cart_list_price{
        font-size: 0;
        span:nth-of-type(1){
          @include sc(.6rem, #f60);
          font-family: Helvetica Neue,Tahoma;

        }
        span:nth-of-type(2){
          @include sc(.7rem, #f60);
          font-family: Helvetica Neue,Tahoma;
          font-weight: bold;
        }
      }
      .cart_list_control{
        display: flex;
        align-items: center;
        span{
          display: flex;
          align-items: center;
          justify-content: center;
        }
        svg{
          @include wh(.9rem, .9rem);
          fill: #3190e8;
        }
        .specs_reduce_icon{
          fill: #999;
        }
        .cart_num{
          @include sc(.65rem, #666);
          min-width: 1rem;
          text-align: center;
          font-family: Helvetica Neue,Tahoma;
        }
      }
    }
  }
}
.screen_cover{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0,.3);
  z-index: 11;
}
.change_show_type{
  display: flex;
  background-color: $fc;
  padding: .1rem 0 .3rem;
  border-bottom: 1px solid #ebebeb;
  div{
    flex: 1;
    text-align: center;
    span{
      @include sc(.65rem, #666);
      padding: .2rem .1rem;
      border-bottom: 0.12rem solid #fff;
    }
    .activity_show{
      color: #3190e8;
      border-color: #3190e8;
    }
  }
}
.rating_container{
  flex: 1;
  overflow-y: hidden;
  flex-direction: column;
  p, span, li, time{
    font-family: Helvetica Neue,Tahoma,Arial;
  }
  .rating_header{
    display: flex;
    background-color: #fff;
    padding: .8rem .5rem;
    margin-bottom: 0.5rem;
    .rating_header_left{
      flex: 3;
      text-align: center;
      p:nth-of-type(1){
        @include sc(1.2rem, #f60);
      }
      p:nth-of-type(2){
        @include sc(.65rem, #666);
        margin-bottom: .1rem;
      }
      p:nth-of-type(3){
        @include sc(.4rem, #999);
      }
    }
    .rating_header_right{
      flex: 4;
      p{
        font-size: .65rem;
        line-height: 1rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        span:nth-of-type(1){
          color: #666;
          margin-right: .5rem;
        }
        .rating_num{
          width: 3rem;
          @include sc(.55rem, #f60);
        }
        .delivery_time{
          @include sc(.4rem, #999);
        }
      }
    }
  }
  .tag_list_ul{
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    padding: .5rem;
    li{
      @include sc(.6rem, #6d7885);
      padding: .3rem .3rem;
      background-color: #ebf5ff;
      border-radius: 0.2rem;
      border: 1px;
      margin: 0 .4rem .2rem 0;
    }
    .unsatisfied{
      background-color: #f5f5f5;
      color: #aaa;
    }
    .tagActivity{
      background-color: #3190e8;
      color: #fff;
    }
  }
  .rating_list_ul{
    background-color: #fff;
    padding: 0 .5rem;
    .rating_list_li{
      border-top: 1px solid #f1f1f1;
      display: flex;
      padding: .6rem 0;
      .user_avatar{
        @include wh(1.5rem, 1.5rem);
        border: 0.025rem;
        border-radius: 50%;
        margin-right: .8rem;
      }
      .rating_list_details{
        flex: 1;
        header{
          display: flex;
          flex: 1;
          justify-content: space-between;
          margin-bottom: .3rem;
          .username_star{
            @include sc(.55rem, #666);
            .username{
              color: #666;
              margin-bottom: .2rem;
            }
            .star_desc{
              display: flex;
              align-items: center;
              .time_spent_desc{
                @include sc(.55rem, #666)
                margin-left: .15rem;
              }
            }
          }
          .rated_at{
            @include sc(.4rem, #999);
          }
        }
        .food_img_ul{
          display: flex;
          flex-wrap: wrap;
          margin-bottom: .4rem;
          li{
            img{
              @include wh(3rem, 3rem);
              margin-right: .4rem;
              display: block;
            }
          }
        }
        .food_name_ul{
          display: flex;
          flex-wrap: wrap;
          li{
            @include sc(.55rem, #999);
            width: 2.2rem;
            padding: .2rem;
            border: 0.025rem solid #ebebeb;
            border-radius: 0.15rem;
            margin-right: .3rem;
            margin-bottom: 4px;
          }
        }
      }
    }
  }
}
.specs_cover{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.4);
  z-index: 17;
}
.specs_list{
  position: fixed;
  top: 35%;
  left: 15%;
  width: 70%;
  background-color: #fff;
  z-index: 18;
  border: 1px;
  border-radius: 0.2rem;
  .specs_list_header{
    h4{
      @include sc(.7rem, #222);
      font-weight: normal;
      text-align: center;
      padding: .5rem;
    }
    .specs_cancel{
      position: absolute;
      right:.5rem;
      top:.3rem;
      color: $icon;
      font-size: .6rem;
    }
  }
  .specs_details{
    padding: .5rem;
    .specs_details_title{
      @include sc(.6rem, #666);
    }
    ul{
      display: flex;
      flex-wrap: wrap;
      padding: .4rem 0;
      li{
        font-size: .6rem;
        padding: .3rem .5rem;
        border: 0.025rem solid #ddd;
        border-radius: .2rem;
        margin-right: .5rem;
        margin-bottom: .2rem;
      }
      .specs_activity{
        border-color: #3199e8;
        color: #3199e8;
      }
    }
  }
  .specs_footer{
    @include fj;
    align-items: center;
    background-color: #f9f9f9;
    padding: 0.5rem;
    border: 1px;
    border-bottom-left-radius: .2rem;
    border-bottom-right-radius: .2rem;
    .specs_price{
      span{
        color: #ff6000;
      }
      span:nth-of-type(1){
        font-size: .5rem;
      }
      span:nth-of-type(2){
        font-size: .8rem;
        font-weight: bold;
        font-family: Helvetica Neue,Tahoma;
      }
    }
    .specs_addto_cart{
      @include wh(4rem, 1.3rem);
      background-color: #3199e8;
      border: 1px;
      border-radius: 0.15rem;
      @include sc(.6rem, #fff);
      text-align: center;
      line-height: 1.3rem;
    }
  }
}
.show_delete_tip{
  position: fixed;
  top: 50%;
  left: 15%;
  width: 70%;
  transform: translateY(-50%);
  background-color: rgba(0,0,0,.8);
  z-index: 18;
  @include sc(.65rem, #fff);
  text-align: center;
  padding: .5rem 0;
  border: 1px;
  border-radius: 0.25rem;
}
.move_dot{
  position: fixed;
  bottom: 30px;
  left: 30px;

  svg{
    @include wh(.9rem, .9rem);
    fill: #3190e8;
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.fade-choose-enter-active, .fade-choose-leave-active {
  transition: opacity .5s;
}
.fade-choose-leave, .fade-choose-leave-active {
  display: none;
}
.fade-choose-enter, .fade-choose-leave-active {
  opacity: 0;
}
.router-slid-enter-active, .router-slid-leave-active {
  transition: all .4s;
}
.router-slid-enter, .router-slid-leave-active {
  transform: translate3d(2rem, 0, 0);
  opacity: 0;
}
.toggle-cart-enter-active, .toggle-cart-leave-active {
  transition: all .3s ease-out;
}
.toggle-cart-enter, .toggle-cart-leave-active {
  transform: translateY(100%);
}
</style>



